* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 90%;
}

.card-grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr 1fr;
}
.card {
  align-content: center;
  border: 1px gray solid;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  max-width: 400px;
}
.card p {
  padding: 3px;
  text-align: center;
}
.card img {
  max-width: 100%;
}
